<!-- 컴포넌트 UI 정의 -->
<template>
  <v-card flat>
    <v-container>
      <v-row class="justify-space-between no-gutters"
        ><v-col class="align-self-center"><div class="font-weight-black ml-3">문의</div></v-col><v-spacer></v-spacer
        ><v-col><v-btn active-class="no-active" text plain @click="dialogOn($store.getters.getMemberId !== null)"> + 문의하기 </v-btn></v-col></v-row
      >
    </v-container>

    <v-expansion-panels
      ><v-expansion-panel v-for="(qna, index) in qnaList" :key="index">
        <v-expansion-panel-header disable-icon-rotate>
          <div>
            <span class="mr-3">{{ qna.id }}</span>
            <span class="text-grey font-weight-light">{{ qna.qregdate }}</span>

            <div class="font-weight-bold mt-2">{{ qna.qtitle }} <v-icon v-if="qna.islock == 'Y'">mdi-lock-outline</v-icon></div>
          </div>
          <template v-slot:actions>
            <div :class="{ 'brown--text font-weight-bold': qna.isanswer == '답변 완료' }">{{ qna.isanswer }}</div>
          </template>
        </v-expansion-panel-header>
        <v-expansion-panel-content color="#f8f3f0">
          <div class="mt-5">{{ qna.qcontent }}</div>
        </v-expansion-panel-content>
        <v-expansion-panel-content color="#f8f3f0" v-if="qna.answerdate != null"
          ><v-divider class="mb-3"></v-divider><span class="mr-2">└ </span> <span class="mr-2">[ LKL MALL ]</span>
          <span class="font-weight-light">{{ qna.answerdate }}</span>
          <div class="font-weight-bold"><span class="mr-2"></span>{{ qna.answercontent }}</div>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <qna-dialog :dialog="dialog" :pcode="pcode" @dialog-close="dialogClose" @reset-qna="resetQna()" />
    <product-dialog @close-dialog="closeLoginDialog()" :dialog="loginDialog" :dialogMsg="loginDialogMessage" />
  </v-card>
</template>

<script>
import apiProduct from "@/apis/product";
import QnaDialog from "@/components/dialog/QnaDialog.vue";
import ProductDialog from "@/components/dialog/ProductDialog.vue";
export default {
  // 컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "",
  // 추가하고 싶은 컴포넌트 등록(import something from "/path")
  components: { QnaDialog, ProductDialog },
  // 컴포넌트 데이터 정의
  data() {
    return { qnaList: [], pager: {}, dialog: false, loginDialogMessage: "", loginDialog: false };
  },
  // 컴포넌트 메소드 정의
  methods: {
    resetQna() {
      console.log("실행");
      this.$emit("reset-qna");
      if (this.$store.getters.getMno != null) {
        this.getProductQna(1, this.$store.getters.getMno, this.pcode);
      } else {
        this.getProductQna(1, 0, this.pcode);
      }
    },
    getProductQna(pageno, mno, pcode) {
      apiProduct
        .getProductQnaList(pageno, mno, pcode)
        .then((response) => {
          let data = response.data;
          this.qnaList = data.productQnaList;
          this.pager = data.pager;
        })
        .catch((error) => {
          if (error.response) {
            console.log(error.response.data);
          } else {
            console.log("네트워크 문제");
          }
        });
    },
    dialogOn(isLogin) {
      if (!isLogin) {
        this.openLoginDialog("needLogin");
      } else {
        this.dialog = true;
      }
    },
    dialogClose() {
      this.dialog = false;
    },
    openLoginDialog(msg) {
      this.loginDialogMessage = msg;
      this.loginDialog = true;
    },
    closeLoginDialog() {
      this.loginDialog = false;
    },
  },
  props: ["pcode"],
  created() {
    if (this.$store.getters.getMno != null) {
      this.getProductQna(1, this.$store.getters.getMno, this.pcode);
    } else {
      this.getProductQna(1, 0, this.pcode);
    }
  },
};
</script>

<!-- 컴포넌트 스타일 정의 -->
<style scoped>
.v-btn--active.no-active::before {
  opacity: 0 !important;
}
</style>
