<!-- 컴포넌트 UI 정의 -->
<template>
  <v-container class="mt-4 pt-0">
    <v-row justify="space-between">
      <v-col
        ><h1 class="font-weight-bold text-h5 mb-1">{{ category }}</h1></v-col
      >
    </v-row>

    <!-- 세부 카테고리 시작 -->
    <v-container fluid class="ma-0 pa-0">
      <v-sheet class="mx-auto mt-2" max-width="700">
        <v-row no-gutters>
          <v-col>
            <v-slide-group>
              <v-slide-item v-slot="{ active, toggle }"
                ><v-btn
                  class="mx-1"
                  :input-value="active"
                  active-class="grey white--text"
                  depressed
                  rounded
                  replace
                  exact
                  @click="toggle"
                  v-on:click="replaceSubCategory(mainCategory)"
                >
                  ALL
                </v-btn>
              </v-slide-item>
              <v-slide-item v-for="subCategory in categoryList" :key="subCategory.priority" v-slot="{ active, toggle }">
                <v-btn
                  class="mx-1"
                  :input-value="active"
                  active-class="grey white--text"
                  depressed
                  rounded
                  replace
                  exact
                  @click="toggle"
                  :to="$route.path + '?ccode=' + mainCategory + '_' + subCategory.cname"
                >
                  {{ subCategory.cname }}
                </v-btn>
              </v-slide-item>
            </v-slide-group>
          </v-col>
          <v-col cols="12" class="mx-3 mt-3 d-flex">
            <div class="mr-2">총</div>
            <div class="main-txt mr-1">{{ wonChange(totalRows) }}</div>
            <div>개</div>
          </v-col>
        </v-row>
      </v-sheet>
    </v-container>

    <!-- 상품 리스트 시작 -->
    <v-container fluid>
      <v-item-group v-model="selected" multiple>
        <v-row dense>
          <v-col v-for="(product, index) in productList" :key="index" :cols="6">
            <v-item>
              <v-card tile elevation="0" class="mb-5">
                <v-img :src="product.color[0].imgurl1" class="white--text" height="200px">
                  <v-row>
                    <v-col class="d-flex justify-end">
                      <v-btn icon dark @click="iWish(product.pcode)">
                        <v-icon>
                          {{ checkWish(product.pcode) ? "mdi-heart" : "mdi-heart-outline" }}
                        </v-icon>
                      </v-btn>
                    </v-col>
                  </v-row>
                  <v-container @click="print(product.pcode)" fill-height class="pa-0"> </v-container>
                </v-img>
                <div class="font-weight-light font-14px">{{ product.pbrand }}</div>
                <div class="font-weight-bold font-14px">{{ product.pname }}</div>
                <div class="font-14px" :class="product.pdiscount == 0 ? '' : 'grey--text'">
                  {{ wonChange(product.pprice) }}<span class="ml-2 main-txt" v-if="product.pdiscount != 0">{{ product.pdiscount }}%</span>
                </div>
                <div class="font-14px" v-if="product.pdiscount != 0">
                  {{ wonChange(product.pprice - Math.ceil(product.pprice * (product.pdiscount / 100))) }}
                </div>
              </v-card>
            </v-item>
          </v-col>
        </v-row>
      </v-item-group>
    </v-container>
    <!-- 상품 리스트 끝 -->
    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
  </v-container>
</template>

<script>
// import axios from "axios";
import apiProduct from "@/apis/product";
import apiSubCategory from "@/apis/category";
import WishApi from "@/apis/wish";
export default {
  // 컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "ProductList",
  // 추가하고 싶은 컴포넌트 등록(import something from "/path")
  components: {},
  // 컴포넌트 데이터 정의
  data() {
    return {
      active: false, // 세부 카테고리 클릭 active
      tab: null, // brand Category tab 기능 현재 구현 X
      model: null, // brand Category tab 기능 현재 구현 X
      overlay: false,
      items: ["BRAND", "CATEGORY"], // brand Category tab 기능 현재 구현 X 추후 필터로 사용
      selected: [], // 찜하기 선택 리스트
      bottom: false, // 하단 체크 변수
      mainCategory: null,
      category: this.$route.query.ccode, // 현재 카테고리
      categoryList: [],
      productList: [], // 불러온 상품 목록
      pageNo: 1, // 현재 불러온 페이지 번호
      totalPageNo: 1, // 최대 페이지 번호
      totalRows: 0, // 총 상품 수
      doubleCheck: false,
    };
  },
  // 컴포넌트 메소드 정의
  methods: {
    // 하단 감지
    bottomVisible() {
      const scrollY = window.scrollY;
      const visible = document.documentElement.clientHeight;
      const pageHeight = document.documentElement.scrollHeight;
      const bottomOfPage = visible + scrollY >= pageHeight;
      return bottomOfPage || pageHeight < visible;
    },
    getSubCategory() {
      apiSubCategory.getSubCategory(sessionStorage.getItem("mainCategory")).then((response) => {
        let data = response.data;
        this.categoryList = data;
      });
    },
    // 하위 카테고리 호출
    replaceSubCategory(ccode) {
      this.$router
        .replace({
          path: this.$route.path,
          query: {
            ccode: ccode,
          },
        })
        .catch((error) => {
          if (error.response) {
            console.log(error.response.data);
          }
        });
    },
    // 상품리스트 호출
    getProductList() {
      this.overlay = true;
      apiProduct
        .getProductList(this.category, this.pageNo)
        .then((response) => {
          this.overlay = false;
          let data = response.data;
          this.totalPageNo = data.pager.totalPageNo;
          this.totalRows = data.pager.totalRows;
          let productList = data.productList;
          productList.forEach((product) => {
            this.productList.push(product);
          });
          this.pageNo++;
          this.mainCategory = sessionStorage.getItem("mainCategory");
          sessionStorage.setItem("productList", JSON.stringify(this.productList));
          sessionStorage.setItem("subCategory", this.category);
          sessionStorage.setItem("visitedDetail", false);
          sessionStorage.setItem("totalPageNo", this.totalPageNo);
          sessionStorage.setItem("totalRows", this.totalRows);
          sessionStorage.setItem("pageNo", this.pageNo);
          this.getSubCategory();
        })
        .catch((error) => {
          this.overlay = false;
          if (error.response) {
            console.log(error.response.data);
          }
        });
    },
    wonChange(num) {
      return String(num).replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
    },
    print(val) {
      this.$router.push(`/product/productdetail?pcode=${val}`);
    },
    checkSession() {
      if (
        sessionStorage.getItem("visitedDetail") == "true" &&
        sessionStorage.getItem("productList") != null &&
        sessionStorage.getItem("subCategory") == this.category
      ) {
        this.productList = JSON.parse(sessionStorage.getItem("productList"));
        this.pageNo = sessionStorage.getItem("pageNo");
        this.mainCategory = sessionStorage.getItem("mainCategory");
        this.totalRows = sessionStorage.getItem("totalRows");
        this.totalPageNo = sessionStorage.getItem("totalPageNo");
        sessionStorage.setItem("subCategory", this.category);
        sessionStorage.setItem("visitedDetail", false);
        this.getSubCategory();
      } else {
        // 세션에 데이터가 유효하지 않은 경우
        if (Number(this.pageNo) <= Number(this.totalPageNo)) {
          this.mainCategory = sessionStorage.getItem("mainCategory");
          this.getProductList();
        }
      }
    },
    async iWish(val) {
      // db와 store에 wish 변경 사항 저장
      if (this.$store.getters.getMno != null && this.$store.getters.getMno != 0) {
        const li = this.$store.getters.getWishlist;
        if (li == null || !li.includes(val)) {
          // add
          console.log("add");
          this.$store.commit("addWishlist", val);
          try {
            const promise = await WishApi.addWish({ mno: this.$store.getters.getMno, pcode: val });
            apiProduct.updateLike({ pcode: val, amount: 1 });
            console.log(promise);
          } catch (error) {
            console.log(error);
          }
        } else {
          console.log("delete");
          this.$store.commit("removeWishlist", val);
          try {
            const promise = await WishApi.deleteWish({ mno: this.$store.getters.getMno, pcode: val });
            apiProduct.updateLike({ pcode: val, amount: -1 });
            console.log(promise);
          } catch (error) {
            console.log(error);
          }
        }
      } else {
        console.log("로그인 필요");
      }
    },
    checkWish(val) {
      // wish 여부 확인
      const li = this.$store.getters.getWishlist;
      if (li == null) {
        return false;
      } else {
        return li.includes(val);
      }
    },
  },
  watch: {
    bottom(bottom) {
      if (bottom && !this.doubleCheck) {
        this.checkSession();
      } else {
        this.doubleCheck = false;
      }
    },
    $route() {
      this.doubleCheck = true;
      this.productList = [];
      this.category = this.$route.query.ccode;
      sessionStorage.setItem("subCategory", this.category);
      this.pageNo = 1;
      this.checkSession();
    },
  },
  created() {
    window.addEventListener("scroll", () => {
      this.bottom = this.bottomVisible();
    });
    this.checkSession();
  },
};
</script>

<!-- 컴포넌트 스타일 정의 -->
<style scoped>
.active-tab {
  color: black;
  font-weight: bold;
}
</style>
