import axios from "axios";
const baseUrl = "http://kosa1.iptime.org:50419/";
function getcartlist(mno) {
  return axios.get(baseUrl+"cart/list?mno=" + mno);
}

function getcartoption(pcode) {
  return axios.get(baseUrl+"cart/option?pcode=" + pcode);
}

function addCart(cart) {
  return axios.post(baseUrl+"cart/addcart", {
    pcode: cart.pcode,
    cartsize: cart.cartsize,
    cartcolor: cart.cartcolor,
    cartamount: cart.cartamount,
    mno: cart.mno,
  });
}
function modifyOpction(cartProduct) {
  return axios.patch(baseUrl+"cart/update", {
    cartcolor: cartProduct.cartcolor,
    cartsize: cartProduct.cartsize,
    cartamount: cartProduct.cartamount,
    cartno: cartProduct.cartno,
  });
}

function getorderProduct(cartno) {
  return axios.get(baseUrl+"cart/form?cartno=" + cartno);
}

function deleteCart(cart){
  return axios.patch(baseUrl+"cart/delete", {
    cartno: cart.cartno,
    mno: cart.mno,
  })
}
export default {
  getcartlist,
  getcartoption,
  addCart,
  modifyOpction,
  getorderProduct,
  deleteCart
};
