<!-- 컴포넌트 UI 정의 -->
<template>
  <div>
    <v-tabs fixed-tabs color="black" v-model="tab">
      <v-tab> 상세 정보 </v-tab>
      <v-tab> 리뷰 </v-tab>
      <v-tab> QnA({{ qnaCount }}) </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item
        ><v-card flat
          ><v-card-text>{{ pcontent }}</v-card-text>
        </v-card></v-tab-item
      >
      <v-tab-item
        ><v-card flat> <v-card-text>리뷰 탭 입니다.</v-card-text> </v-card></v-tab-item
      >
      <v-tab-item><product-qa :pcode="pcode" @reset-qna="setQnaNum()" /></v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
import ProductQa from "./ProductQa.vue";
import apiProduct from "@/apis/product";
export default {
  // 컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "",
  // 추가하고 싶은 컴포넌트 등록(import something from "/path")
  components: { ProductQa },
  // 컴포넌트 데이터 정의
  data() {
    return { tab: null, qnaCount: 0 };
  },
  // 컴포넌트 메소드 정의
  methods: {
    setQnaNum() {
      apiProduct
        .getProductQnaNum(this.$route.query.pcode)
        .then((response) => {
          this.qnaCount = response.data;
          console.log(this.qnaCount);
          console.log(response.data);
        })
        .catch((error) => {
          if (error.response) {
            console.log(error.response.data);
          } else {
            console.log("네트워크 문제");
          }
        });
    },
  },
  created() {
    this.setQnaNum();
  },
  props: ["pcontent", "pcode"],
};
</script>

<!-- 컴포넌트 스타일 정의 -->
<style scoped></style>
