<!-- 컴포넌트 UI 정의 -->
<template>
  <v-container class="ma-0 pa-0">
    <v-expand-transition>
      <v-sheet
        v-show="sheet"
        color="white"
        class="text-center rounded-tr-xl"
        height="250px"
        width="100%"
        style="position: fixed; bottom: 0; padding-bottom: 56px; z-index: 6"
      >
        <v-btn class="mt-3" text color="#c2935f" @click="sheet = false"> close </v-btn>
        <v-container>
          <v-row no-gutters>
            <v-col cols="12"
              ><v-select color="#333333" item-color="grey" v-model="selectPsize" :items="colorAmountList" label="사이즈"> </v-select
            ></v-col>
            <v-col cols="12"></v-col>
          </v-row>
          <v-row no-gutters align-content="center" class="align-center">
            <v-col>
              <!-- 수량 변경 -->
              <v-card-actions v-if="selectPsize">
                <div>
                  <v-btn depressed color="white" @click="decreaseScore()">
                    <v-icon> mdi-minus </v-icon>
                  </v-btn>
                </div>
                <v-spacer></v-spacer>
                <h3>{{ selectAmount }}</h3>
                <v-spacer></v-spacer>
                <div>
                  <v-btn depressed color="white" @click="increaseScore()">
                    <v-icon> mdi-plus </v-icon>
                  </v-btn>
                </div>
              </v-card-actions></v-col
            >
            <v-col
              ><div v-if="selectPsize" class="text-h5 text-center brown--text">{{ wonChange(psaleprice * selectAmount) }}</div></v-col
            >
          </v-row>
        </v-container>
      </v-sheet>
    </v-expand-transition>

    <v-bottom-navigation grow app v-if="flag" class="ma-0 pa-0 fill-height" style="z-index: 7">
      <v-row class="text-center ma-0 pa-0" align-content="center" no-gutters>
        <v-col cols="4" style="background: #333333" class="fill-height"
          ><v-btn block>
            <span class="font-weight-bold"></span>
            <v-icon color="white" @click="iWish(pcode)">
              {{ isWish ? "mdi-heart" : "mdi-heart-outline" }}
            </v-icon>
          </v-btn>
        </v-col>
        <v-col cols="8" style="background: #c2935f" class="fill-height">
          <v-btn block @click="addCart($store.getters.getMemberId !== null)">
            <v-icon color="white">mdi-shopping-outline</v-icon>
          </v-btn>
        </v-col>
      </v-row>
    </v-bottom-navigation>
    <v-overlay @click="close()" :value="sheet"></v-overlay>
    <product-dialog @close-dialog="closeDialog()" :dialog="dialog" :dialogMsg="dialogMsg" :maxAmount="selectAmount" />
  </v-container>
</template>

<script>
import ProductDialog from "@/components/dialog/ProductDialog.vue";
import CartApi from "@/apis/cart";
import WishApi from "@/apis/wish";
import apiProduct from "@/apis/product";
export default {
  // 컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "ProductDetailFooter",
  // 추가하고 싶은 컴포넌트 등록(import something from "/path")
  components: { ProductDialog },
  // 컴포넌트 데이터 정의
  data() {
    return {
      dialog: false,
      dialogMsg: "fail",
      overlay: false,
      isWish: false,
      sheet: false,
      selectPsize: null,
      selectAmount: 1,
      colorAmountList: [],
      cart: {
        pcode: "",
        cartsize: "",
        cartcolor: "",
        cartamount: "",
        mno: "",
      },
    };
  },
  // 컴포넌트 메소드 정의
  methods: {
    addCart(isLogin) {
      // 장바구니 담기 전 로그인 여부를 확인
      if (!isLogin) {
        this.openDialog("needLogin");
        this.$router.push("/member/login").catch(() => {});
      } else {
        if (this.sheet) {
          // 장바구니 탭이 열려있을 때
          // Validation Check 선택 잘 되었는지 확인
          if (this.selectPsize) {
            // 장바구니 추가 api 호출 then 안에 dialog 추가
            this.addCartAxios();
            this.sheet = false;
          } else {
            this.openDialog("noSize");
          }
        } else {
          // 상품사이즈 리스트 초기화
          this.colorAmountList = [];
          this.amountList.forEach((element) => {
            let isZeroAmount = false;
            let psize = element.psize;
            if (this.amountDict[element.psize] <= 0) {
              isZeroAmount = true;
              psize += " - 품절";
            }
            this.colorAmountList.push({ text: psize, value: psize, disabled: isZeroAmount });
          });
          this.sheet = true;
        }
      }
    },
    addCartAxios() {
      this.cart.pcode = this.pcode;
      this.cart.cartsize = this.selectPsize.toString();
      this.cart.cartcolor = this.selectColor;
      this.cart.cartamount = this.selectAmount;
      this.cart.mno = this.$store.getters.getMno;
      CartApi.addCart(this.cart)
        .then((response) => {
          this.openDialog(response.data.msg);
        })
        .catch((error) => {
          if (error.response) {
            console.log(error.response.data);
          } else {
            console.log("네트워크 문제");
          }
        });
    },
    increaseScore() {
      this.selectAmount++;
      if (this.selectAmount > this.amountDict[this.selectPsize]) {
        this.selectAmount = this.amountDict[this.selectPsize];
        this.openDialog("overAmount");
      }
    },
    decreaseScore() {
      this.selectAmount--;
      if (this.selectAmount < 1) {
        this.selectAmount = 1;
      }
    },
    async iWish(val) {
      // db와 store에 wish 변경 사항 저장
      if (this.$store.getters.getMno != null && this.$store.getters.getMno != 0) {
        const li = this.$store.getters.getWishlist;
        if (li == null || !li.includes(val)) {
          // add
          console.log("add");
          this.$store.commit("addWishlist", val);
          try {
            const promise = await WishApi.addWish({ mno: this.$store.getters.getMno, pcode: val });
            apiProduct.updateLike({ pcode: val, amount: 1 });
            console.log(promise);
          } catch (error) {
            console.log(error);
          }
        } else {
          console.log("delete");
          this.$store.commit("removeWishlist", val);
          try {
            const promise = await WishApi.deleteWish({ mno: this.$store.getters.getMno, pcode: val });
            apiProduct.updateLike({ pcode: val, amount: 1 });
            console.log(promise);
          } catch (error) {
            console.log(error);
          }
        }
        this.checkWish();
      } else {
        this.openDialog("needLogin");
      }
    },
    checkWish() {
      // wish 여부 확인
      const li = this.$store.getters.getWishlist;
      if (li == null) {
        this.isWish = false;
      } else {
        this.isWish = li.includes(this.pcode);
      }
    },
    wonChange(num) {
      return String(num).replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
    },
    close() {
      this.sheet = false;
    },
    openDialog(msg) {
      this.dialogMsg = msg;
      this.dialog = true;
    },
    closeDialog() {
      this.dialog = false;
      this.dialogMsg = null;
    },
  },
  watch: {
    sheet() {
      if (this.sheet) {
        document.documentElement.style.overflow = "hidden";
      } else {
        document.documentElement.style.overflow = "auto";
        this.selectPsize = null;
      }
    },
    selectPsize() {
      this.selectAmount = 1;
    },
  },
  created() {
    this.checkWish();
  },
  props: ["flag", "amountList", "amountDict", "psaleprice", "selectColor", "pcode"],
};
</script>

<!-- 컴포넌트 스타일 정의 -->
<style scoped></style>
