<!-- 컴포넌트 UI 정의 -->
<template>
  <v-container>
    <v-row><v-col class="text-h5 main-head">나의 좋아요</v-col></v-row>
    <div v-if="!hasLike" class="mt-4 grey lighten-3">
      <v-row>
        <v-col class="font-weight-bold" align="center">좋아하는 상품이 없습니다!</v-col>
      </v-row>
      <v-row
        ><v-col align="center"><v-btn color="primary" to="/product/productlist?ccode=WOMEN_OUTER">상품 보러가기</v-btn></v-col></v-row
      >
    </div>
    <div class="d-inline-flex overflow-x-auto" style="width: 100%">
      <v-container>
        <v-row>
          <v-col cols="6" v-for="p in products" :key="p.pcode">
            <v-row class="pa-2">
              <v-img :src="p.color[0].imgurl1" width="120px"
                ><div class="d-flex justify-end">
                  <v-btn icon dark @click="iWish(p.pcode)">
                    <v-icon>
                      {{ checkWish(p.pcode) ? "mdi-heart" : "mdi-heart-outline" }}
                    </v-icon>
                  </v-btn>
                </div></v-img
              >
            </v-row>
            <v-row class="pb-0 pl-2">
              <v-col class="pa-0 font-weight-bold font-14px">{{ p.pbrand }}</v-col>
            </v-row>
            <v-row class="pb-0 pl-2 pt-0 font-weight-bold body-2 font-14px"
              ><v-col class="pa-0">{{ p.pname }}</v-col></v-row
            >
            <v-row>
              <v-col class="pb-0 pt-0 font-14px" :class="p.pdiscount == 0 ? '' : 'grey--text'"
                >{{ String(p.pprice).replace(/\B(?=(\d{3})+(?!\d))/g, ",") }}
              </v-col>
            </v-row>
            <v-row v-if="p.pdiscount != 0">
              <v-col class="pt-0 pb-0 font-14px">{{
                String(p.pprice - Math.ceil(p.pprice * (p.pdiscount / 100))).replace(/\B(?=(\d{3})+(?!\d))/g, ",")
              }}</v-col>
              <v-col class="pt-0 pb-0 main-txt font-14px">{{ p.pdiscount }}%</v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-container>
    </div>
  </v-container>
</template>

<script>
import apiProduct from "@/apis/product";
import WishApi from "@/apis/wish";
export default {
  // 컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "WishProductList",
  // 추가하고 싶은 컴포넌트 등록(import something from "/path")
  components: {},
  // 컴포넌트 데이터 정의
  data() {
    return { products: [], hasLike: true };
  },
  // 컴포넌트 메소드 정의
  methods: {
    async getInfo() {
      this.products = [];
      let likeList = this.$store.getters.getWishlist;
      if (likeList) {
        if (likeList.length < 1) {
          this.hasLike = false;
          return;
        }
        for (let item of likeList) {
          const promise = await apiProduct.getProductDetail(item);
          let product = {};
          console.log(promise.data);
          product = promise.data;
          this.products.push(product);
          this.hasLike = true;
        }
      } else {
        this.hasLike = false;
      }
    },
    move(val) {
      this.$router.push("/product/productdetail?pcode=" + val);
    },
    async iWish(val) {
      // db와 store에 wish 변경 사항 저장
      if (this.$store.getters.getMno != null && this.$store.getters.getMno != 0) {
        const li = this.$store.getters.getWishlist;
        if (li == null || !li.includes(val)) {
          // add
          console.log("add");
          this.$store.commit("addWishlist", val);
          try {
            const promise = await WishApi.addWish({ mno: this.$store.getters.getMno, pcode: val });
            apiProduct.updateLike({ pcode: val, amount: 1 });
            console.log(promise);
          } catch (error) {
            console.log(error);
          }
        } else {
          console.log("delete");
          this.removeWishView(val);
          this.$store.commit("removeWishlist", val);
          try {
            const promise = await WishApi.deleteWish({ mno: this.$store.getters.getMno, pcode: val });
            apiProduct.updateLike({ pcode: val, amount: -1 });
            console.log(promise);
          } catch (error) {
            console.log(error);
          }
        }
      } else {
        console.log("로그인 필요");
      }
    },
    checkWish(val) {
      // wish 여부 확인
      const li = this.$store.getters.getWishlist;
      if (li == null) {
        return false;
      } else {
        return li.includes(val);
      }
    },
    removeWishView(val) {
      for (let i = 0; i < this.products.length; i++) {
        if (this.products[i].pcode == val) {
          this.products.splice(i, 1);
        }
      }
    },
  },
  created() {
    this.getInfo();
    console.log(this.hasLike);
  },
};
</script>

<!-- 컴포넌트 스타일 정의 -->
<style scoped></style>
