<!-- 컴포넌트 UI 정의 -->
<template>
  <v-row>
    <v-dialog v-model="dialog" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="text-h5">상품 QnA</span>
        </v-card-title>
        <v-card-text>
          <div>{{ pcode }}</div>
          <v-container>
            <v-row>
              <v-col cols="12" sm="6" md="4">
                <v-text-field color="brown" v-model="qtitle" label="문의 제목 *" required></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-textarea color="brown" v-model="qcontent" label="문의 내용 *"> </v-textarea>
              </v-col>
              <v-col> <v-checkbox color="brown" v-model="islock" label="비밀글 선택"></v-checkbox></v-col>
            </v-row>
          </v-container>
          <small>* 필수 항목 입니다.</small>
          <v-alert v-model="alert" dense outlined type="error" transition="scale-transition">
            {{ alertMessage }}
          </v-alert>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="black darken-1" text @click="close"> Close </v-btn>
          <v-btn color="brown darken-1" text @click="addQna"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import apiProduct from "@/apis/product";
export default {
  // 컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "",
  // 추가하고 싶은 컴포넌트 등록(import something from "/path")
  components: {},
  // 컴포넌트 데이터 정의
  data() {
    return {
      alertMessage: "",
      alert: false,
      qtitle: "",
      qcontent: "",
      islock: false,
      qnaInfo: {
        pcode: "",
        qtitle: "",
        qcontent: "",
        islock: "",
        mno: "",
        id: "",
      },
    };
  },
  // 컴포넌트 메소드 정의
  methods: {
    close() {
      this.$emit("dialog-close");
    },
    addQna() {
      if (this.qtitle.length == 0) {
        this.alertMessage = "문의 제목을 입력해주세요.";
        this.alert = true;
        setTimeout(() => {
          this.alert = false;
        }, 2000);
      } else if (this.qcontent.length == 0) {
        this.alertMessage = "문의 내용을 입력해주세요.";
        this.alert = true;
        setTimeout(() => {
          this.alert = false;
        }, 2000);
      } else {
        this.qnaInfo.pcode = this.pcode;
        this.qnaInfo.qtitle = this.qtitle;
        this.qnaInfo.qcontent = this.qcontent;
        this.qnaInfo.mno = this.$store.getters.getMno;
        this.qnaInfo.id = this.$store.getters.getMemberId;
        if (this.islock) {
          this.qnaInfo.islock = "Y";
        } else {
          this.qnaInfo.islock = "N";
        }
        apiProduct
          .addQna(this.qnaInfo)
          .then(() => {
            this.$emit("dialog-close");
            this.$emit("reset-qna");
            console.log("QnaDialog실행");
          })
          .catch((error) => {
            if (error.response) {
              this.alertMessage = "잠시후 다시 시도해 주세요.";
              this.alert = true;
              setTimeout(() => {
                this.alert = false;
              }, 2000);
              console.log(error.response.data);
            } else {
              this.alertMessage = "알 수 없는 문제입니다. 고객센터에 문의해 주세요.";
              this.alert = true;
              setTimeout(() => {
                this.alert = false;
              }, 2000);
              console.log("네트워크 문제");
            }
          });
      }
    },
  },
  props: ["dialog", "pcode"],
};
</script>

<!-- 컴포넌트 스타일 정의 -->
<style scoped></style>
