<!-- 컴포넌트 UI 정의 -->
<template>
  <v-container class="ma-0 pa-0">
    <v-row no-gutters>
      <v-col cols="12"
        ><v-carousel v-model="imgIndex" hide-delimiter-background delimiter-icon="mdi-minus">
          <v-carousel-item v-for="(item, i) in imgurl[selectColor]" :key="i" :src="item">
            <v-img :src="item"></v-img>
          </v-carousel-item> </v-carousel
      ></v-col>
    </v-row>
    <v-row class="mt-n4" no-gutters>
      <v-col
        ><v-card class="mx-auto rounded-tr-xl" max-width="400" tile>
          <v-card-title>
            <v-row no-gutters justify="space-between">
              <v-col cols="12"
                ><div class="text-h6 mb-3">{{ product.pbrand }}</div></v-col
              >
              <v-col cols="12"
                ><div class="text-h5 mb-2">{{ product.pname }}</div></v-col
              >
              <v-col class="d-flex" cols="12"
                ><div class="brown--text text-h5 mr-5">{{ wonChange(psaleprice) }}</div>
                <div v-if="isSale" class="grey--text text-h6 mr-5">{{ wonChange(product.pprice) }}</div>
                <div v-if="isSale" class="black--text text-h6">{{ product.pdiscount }}% OFF</div></v-col
              >
              <v-col></v-col>
            </v-row>
          </v-card-title>

          <v-divider class="mx-4"></v-divider>

          <v-card-text>
            <!-- Color chip 시작 -->
            <span class="subheading">Select color</span>
            <v-item-group mandatory>
              <v-container>
                <v-row no-gutters>
                  <v-item v-for="color in product.color" :key="color.pcolor" v-slot="{ active, toggle }">
                    <v-card
                      color="white"
                      class="mr-3 rounded-circle pa-0"
                      dark
                      @click="toggle"
                      v-on:click="changeColor(color.pcolor)"
                      width="30"
                      height="30"
                    >
                      <v-img :src="color.colorurl" class="d-flex text-center align-center rounded-circle"
                        ><v-icon>
                          {{ active ? "mdi-check" : "" }}
                        </v-icon></v-img
                      >
                    </v-card>
                  </v-item>
                </v-row>
              </v-container>
            </v-item-group>
            <!-- Color chip 끝 -->
            <!-- Size 시작 -->
            <span class="subheading">Check size amount</span>
            <v-chip-group class="ml-2">
              <v-chip
                v-for="(amount, i) in amountList"
                :key="i"
                :value="amount.psize"
                outlined
                :color="checkSizeAmount(amount.psize) ? 'brown' : 'grey'"
                :disabled="checkSizeAmount(amount.psize) ? false : true"
              >
                {{ amount.psize }}
              </v-chip>
            </v-chip-group>
            <!-- Size 끝 -->
          </v-card-text>
          <v-divider class="mx-4" />
          <product-common :pcode="pcode" />
          <product-detail-tabs :pcontent="product.pcontent" :pcode="product.pcode" /> </v-card
      ></v-col>
    </v-row>
    <product-detail-footer
      :flag="flag"
      :amountList="amountList"
      :amountDict="amountDict"
      :psaleprice="psaleprice"
      :selectColor="selectColor"
      :pcode="pcode"
    />
    <!-- 하단 장바구니 탭 시작 -->
    <!-- 하단 장바구니 탭 끝 -->
  </v-container>
</template>

<script>
import apiProduct from "@/apis/product";
import ProductDetailFooter from "./ProductDetailFooter.vue";
import ProductCommon from "./ProductCommon.vue";
import ProductDetailTabs from "./ProductDetailTabs.vue";

export default {
  // 컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "ProductDetail",
  // 추가하고 싶은 컴포넌트 등록(import something from "/path")
  components: { ProductDetailFooter, ProductCommon, ProductDetailTabs },
  // 컴포넌트 데이터 정의
  data() {
    return {
      pcode: this.$route.query.pcode,
      product: {},
      imgurl: {},
      imgIndex: 0,
      psaleprice: 0,
      pcontent: null,
      isSale: false,
      selectColor: null,
      amountDict: {}, // 수량 확인을 위한 데이터
      amountList: {}, // 수량 출력을 위한 데이터
      flag: true,
      scrollPosition: 0,
      beforePosition: 0,
    };
  },
  // 컴포넌트 메소드 정의
  methods: {
    wonChange(num) {
      return String(num).replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
    },
    setSalePrice() {
      this.psaleprice = this.product.pprice;
      if (this.product.pdiscount != 0) {
        this.isSale = true;
        this.psaleprice = Math.floor((this.product.pprice * ((100 - this.product.pdiscount) / 100)) / 100) * 100;
      }
    },
    changeColor(pcolor) {
      this.selectColor = pcolor;
      this.imgIndex = 0;
      this.setAmountDict(this.pcode, pcolor);
    },
    // 현재 선택한 컬러의 사이즈 받아오기
    setAmountDict(pcode, pcolor) {
      apiProduct
        .getProductSizeAmount(pcode, pcolor)
        .then((response) => {
          let amountList = response.data.amount;
          amountList.forEach((amount) => {
            this.amountDict[amount.psize] = amount.pamount;
          });
          this.amountList = amountList;
          this.sizeSort(this.amountList);
        })
        .catch((error) => {
          if (error.response) {
            console.log(error.response.data);
          } else {
            console.log("네트워크 문제");
          }
        });
    },
    checkSizeAmount(size) {
      if (this.amountDict[size] < 1) {
        return false;
      } else {
        return true;
      }
    },
    // Size 텍스트 정렬
    sizeSort(arr) {
      var sizeDict = {
        XSS: -1,
        XXS: 0,
        XS: 1,
        S: 2,
        MS: 3,
        M: 4,
        ML: 5,
        L: 6,
        XL: 7,
        XXL: 8,
        XXXL: 9,
        FR: 100,
      };
      arr.sort(function (a, b) {
        let tempA = a.psize;
        let tempB = b.psize;
        if (isNaN(tempA)) {
          tempA = sizeDict[tempA];
        }
        if (isNaN(tempB)) {
          tempB = sizeDict[tempB];
        }
        if (tempA > tempB) {
          return 1;
        } else if (tempA < tempB) {
          return -1;
        } else {
          return 0;
        }
      });
    },
    recentItem() {
      let rec = sessionStorage.getItem("recent");
      let nowPcode = this.$route.query.pcode;
      if (rec) {
        rec = JSON.parse(rec);
        if (rec.length < 10) {
          if (!rec.includes(nowPcode)) {
            rec.push(nowPcode);
            sessionStorage.setItem("recent", JSON.stringify(rec));
          }
        } else {
          if (!rec.includes(nowPcode)) {
            rec.shift();
            rec.push(nowPcode);
            sessionStorage.setItem("recent", JSON.stringify(rec));
          }
        }
      } else {
        let li = [];
        li.push(nowPcode);
        sessionStorage.setItem("recent", JSON.stringify(li));
      }
    },
    handleScroll(e) {
      this.scrollPosition = e.target.documentElement.scrollTop;
      if (this.scrollPosition == 0 || this.beforePosition <= this.scrollPosition) {
        this.flag = true;
      } else {
        this.flag = false;
      }
      this.beforePosition = this.scrollPosition;
    },
  },
  created() {
    sessionStorage.setItem("visitedDetail", true);
    // 상품 디테일 api 호출
    apiProduct
      .getProductDetail(this.pcode)
      .then((response) => {
        this.product = response.data;
        this.selectColor = this.product.color[0].pcolor;
        // ImgUrl json을 객체 형태로 변환
        this.imgurl = this.product.color.reduce((newObj, obj) => {
          newObj[obj.pcolor] = [obj.imgurl1, obj.imgurl2, obj.imgurl3];
          return newObj;
        }, {});
        this.setAmountDict(this.pcode, this.selectColor); // 수량 Dict 생성
        this.setSalePrice(); // 할인된 가격 계산
        this.recentItem();
      })
      .catch((error) => {
        if (error.response) {
          console.log(error.response.data);
        } else {
          console.log("네트워크 문제");
        }
      });
    window.addEventListener("scroll", this.handleScroll); // 상품 디테일 footer 이벤트 스크롤
  },
  mounted() {
    window.scrollTo(0, 0);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll); // 상품 디테일 footer 이벤트 스크롤 제거
  },
};
</script>

<!-- 컴포넌트 스타일 정의 -->
<style scoped></style>
