<!-- 컴포넌트 UI 정의 -->
<template>
  <v-row no-gutters justify="center">
    <v-dialog v-model="dialog" persistent max-width="290">
      <v-card>
        <v-card-title class="text-h5"> </v-card-title>
        <v-card-text class="text-center">{{ message }}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="close()"> 확인 </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
export default {
  // 컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "ProductDialog",
  // 추가하고 싶은 컴포넌트 등록(import something from "/path")
  components: {},
  // 컴포넌트 데이터 정의
  data() {
    return { message: null };
  },
  props: ["dialog", "dialogMsg", "maxAmount"],
  // 컴포넌트 메소드 정의
  methods: {
    changeMsg(message) {
      switch (message) {
        case "noSize":
          this.message = "사이즈를 선택해 주세요.";
          break;
        case "overAmount":
          this.message = "구매 가능한 재고 " + this.maxAmount + "개만 선택하실 수 있습니다.";
          break;
        case "addCart":
          this.message = "쇼핑백에 담겼습니다.";
          break;
        case "needLogin":
          this.message = "로그인이 필요한 서비스 입니다.";
          break;
        case "duplicated":
          this.message = "쇼핑백에 동일 상품이 존재합니다.";
          break;
        case "fail":
          this.message = "알 수 없는 이유로 실패하였습니다. 잠시 후 다시 시도해 주십시오.";
          break;
      }
    },
    close() {
      this.$emit("close-dialog");
    },
  },
  watch: {
    dialogMsg() {
      this.changeMsg(this.dialogMsg);
    },
  },
};
</script>

<!-- 컴포넌트 스타일 정의 -->
<style scoped></style>
